import { evntBus } from "../../bus";
export default {
  data: () => ({
    loading: false,
    pos_profile: "",
    invoice_doc: "",
  }),

  methods: {
    back_to_invoice() {
      evntBus.$emit("show_payment", "false");
    },
    submit() {
      this.submit_invoice();
      evntBus.$emit("new_invoice", "false");
      this.back_to_invoice();
    },
    submit_invoice() {
      const vm = this;
      frappe.call({
        method: "posawesome.posawesome.api.posapp.submit_invoice",
        args: {
          data: this.invoice_doc,
          to_submit: "True",
        },
        async: true,
        callback: function (r) {
          if (r.message) {
            vm.load_print_page();
            frappe.show_alert(
              // TODO : replace whith proper alert
              {
                message: __(`Invoice ${r.message.name} Submited`),
                indicator: "green",
              },
              5
            );
          }
        },
      });
    },
    load_print_page() {
      const letter_head = this.pos_profile.letter_head || 0;
      const url =
        frappe.urllib.get_base_url() +
        "/printview?doctype=Sales%20Invoice&name=" +
        this.invoice_doc.name +
        "&format=" +
        this.pos_profile.print_format +
        "&no_letterhead=" +
        letter_head;

      // TODO : need better way for printing
      const printWindow = window.open(url, "Print");
      printWindow.addEventListener(
        "load",
        function () {
          printWindow.print();
          // printWindow.close();
          // NOTE : uncomoent this to auto closing printing window
        },
        true
      );
    },
  },

  computed: {
    total_payments() {
      let total = 0;
      this.invoice_doc.payments.forEach((payment) => {
        total += flt(payment.amount);
      });
      return total;
    },
    diff_payment() {
      return this.invoice_doc.grand_total - this.total_payments;
    },
    diff_lable() {
      let lable = this.diff_payment < 0 ? "Change" : "To Be Paid";
      return lable
    },
  },

  created: function () {
    this.$nextTick(function () {
      evntBus.$on("send_invoice_doc_payment", (invoice_doc) => {
        this.invoice_doc = invoice_doc;
        this.invoice_doc.payments[0].amount = invoice_doc.grand_total;
      });
      evntBus.$on("register_pos_profile", (data) => {
        this.pos_profile = data.pos_profile;
      });
    });
  },

  watch: {},
};
